const Events = () =>{
    const data = useStaticQuery(graphql`
      {
        allFile(filter: { sourceInstanceName: { eq: "flyers" } }){
          edges {
            node {
              publicURL
            }
          }
        }
      }
    `)
    return(
      <section id="events" >
      <div className="events-list">
        { data.allFile.edges.map((file, index) => (
          <img className="promotional-flyer" key={`flyer-${index}`} src={file.node.publicURL} alt={file.node.name} />
        ))}
      </div>
      </section>
    )
  }


