import React from "react"
import { Link } from "gatsby"

import SEO from '~/components/seo'

import Twitter from '../../resources/icons/twitter.png'
import Spotify from '../../resources/icons/spotify.png'
import Soundcloud from '../../resources/icons/soundcloud.png'
import Logo from '../../resources/icons/logo.png'
import data from "../../content/links.json"

const imageLinks = (source) => {
  switch(source) {
    case 'twitter':
    return <img src={Twitter} height="64px" width="64px"/>;
    case 'spotify':
      return <img src={Spotify} height="64px" width="64px"/>;
    case 'soundcloud':
      return <img src={Soundcloud} height="64px" width="64px"/>;
    default:
      return <img src={Logo} height="64px" width="64px"/>;
  }
}

const LinkItem = (props) => (
        <li className="link-container" key={props.index}>
          <div className="link-icon">
          {
            imageLinks(props.source)
          }
          </div>
          <a  href={props.url}>
            <h3 className="link-title">{props.title}</h3>
          </a>
        </li>
)

const Links = () => (
  <ul id="links">
    {
    data.links.map((link,index) => ( <LinkItem key={ 'link-' + index.toString() } index={ 'link' + index.toString()} source={link.source} title={link.title} url={link.url} description={link.description} />) )
    }
  </ul>
)
export default Links
